			// 发送验证码并存入获取的电话号码
			var that = this
			if (this.phoneNumber) {
				uni.request({
					url: this.$bashUrl + '/index.php/home/Api/register/',
					method: "POST",
					data: {
						phone: this.phoneNumber,
						r_phone:this.recommendphone
					},
					success(res) {
						that.qrcode = res.data
						uni.redirectTo({
							url: '/pages/decode/decode?id=' + that.qrcode + '&phone=' + that.phoneNumber,
						});
					},
					fail(err) {
						console.log('获取错误')
					}
				})
			} else {
				uni.showToast({
					title: '请您先输入电话',
					duration: 2000
				})
			}
		}
